  background-position: 50%; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/intro@2x.jpg"); } }
  @media (max-width: 1023px) {
    .page-header {
      position: relative;
      background: none; } }

.page-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
