      width: 100%; } }

@media (max-width: 1023px) {
  .page-header img {
    position: absolute;
    top: 25px;
    left: 3%; } }

@media (max-width: 1023px) {
  .page-header img {
    position: absolute;
    top: 25px;
    left: 3%; } }

@media (max-width: 767px) {
