.toggle {
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  width: 50px;
  height: 50px;
  padding: 0;
  border: none; }

@media (max-width: 1023px) {
  .page-header--no-js .toggle {
    display: none; } }

@media (max-width: 1023px) {
  .page-header--closed-menu .toggle {
    display: block;
    background-color: #0ad9c6; } }

@media (max-width: 1023px) {
  .page-header--opened-menu .toggle {
    display: block;
    z-index: 5;
    background-color: transparent; } }

.page-header--closed-menu .toggle__icon--burger,
.page-header--opened-menu .toggle__icon--cross {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.page-header--opened-menu .toggle__icon--burger,
.page-header--closed-menu .toggle__icon--cross {
  display: none; }

